
.search-room
	border: 1px solid black
	display: flex
	width: 350px
	height: 360px
	flex-flow: column nowrap
	justify-content: flex-start
	align-items: flex-start
	background: #FFFFFF
	border: 1px solid rgba(0, 0, 0, 0.12)
	box-sizing: border-box
	box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2)
	border-radius: 4px
	padding: 5px 10px

.search-header
	width: 250px
	height: 60px
	font-family: 'Quicksand', sans-serif
	font-weight: bold
	font-size: 24px
	line-height: 30px
	color: #1F2041
	padding: 0px
	margin: 27px 30px 10px 10px

.search-dates
	display: flex
	flex-flow: row nowrap
	justify-content: space-around
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: bold
	font-size: 12px
	line-height: 15px
	text-transform: uppercase
	color: #1F2041


.search-arrival
	display: flex
	flex-flow: column wrap
	align-content: flex-start
	font-family: 'Montserrat', sans-serif
	margin-left: 10px
	margin-right: 20px

.search-arrival__header,.search-checkout__header,.search-guests__header
	font-family: 'Montserrat', sans-serif
	font-weight: bold
	font-size: 12px
	line-height: 15px
	text-transform: uppercase
	color: #1F2041
	margin-bottom: 5px
	
.search-arrival__input,.search-checkout__input
	border: none 
	outline: none 
	-webkit-appearance: none 
	background: #FFFFFF
	border: 1px solid rgba(31, 32, 65, 0.25)
	box-sizing: border-box
	border-radius: 4px
	color: rgba(31, 32, 65, 0.75)
	width: 140px
	height: 38px
	padding-left: 8px
	font-size: 14px
	background: url(/img/expand.png) no-repeat 90% 60%

.search-guests
	margin: 7px 10px

.search-guests__input
	width: 300px
	height: 38px
	border: none 
	outline: none 
	-webkit-appearance: none 
	background: #FFFFFF
	border: 1px solid rgba(31, 32, 65, 0.25)
	box-sizing: border-box
	border-radius: 4px
	color: rgba(31, 32, 65, 0.75)
	text-align: left
	background: url(/img/expand.png) no-repeat 96% 50%

.search-submit
	border: none 
	outline: none 
	-webkit-appearance: none 
	background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%)
	border-radius: 22px
	width: 300px
	height: 44px
	margin: 27px 10px 0px 10px
	font-family: Montserrat
	font-style: normal
	font-weight: bold
	font-size: 12px
	line-height: 15px
	text-align: center
	text-transform: uppercase
	color: #FFFFFF

.submit-wrap
	display: flex
	flex-flow: row nowrap
	position: relative

.search-submit__img
	position: absolute
	top: 55%
	right: 6%



	
	

	




